import React, { Component } from "react";
import axios from "axios";
import moment from "moment";
import "./App.css";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      temp: 0,
      nama: "",
      negara: "",
      awan: "",
      humidity: "",
      wind: 0,
      icon: "",
      jam: 3600,
      realjam: '',
    };
  }

  componentDidMount() {
    axios
      .get("https://api.openweathermap.org/data/2.5/weather?q=london&appid=de6ec18fc067d0a1cdbbd442be9e5a05")
      .then((response) => {
        this.setState({ nama: response.data.name });
        this.setState({ temp: response.data.main.temp });
        this.setState({ humidity: response.data.main.humidity });
        this.setState({ awan: response.data.weather[0].description });
        this.setState({ icon: response.data.weather[0].icon });
        this.setState({ wind: response.data.wind.speed });
      })
      .catch((error) => {
        console.log("error", error);
      });
  }

  onSubmitHandler = (event) => {
    event.preventDefault();
    axios
      .get(`https://api.openweathermap.org/data/2.5/weather?q=${this.state.negara}&appid=de6ec18fc067d0a1cdbbd442be9e5a05`)
      .then((response) => {
        this.setState({ nama: response.data.name });
        this.setState({ temp: response.data.main.temp });
        this.setState({ humidity: response.data.main.humidity });
        this.setState({ awan: response.data.weather[0].description });
        this.setState({ icon: response.data.weather[0].icon });
        this.setState({ wind: response.data.wind.speed });
        this.setState({ jam: response.data.timezone });
      })
      .catch((error) => {
        console.log(error);
      });
  };

  handlerinputOnChange = (event) => {
    let nama = event.target.name;
    let value = event.target.value;
    this.setState({ [nama]: value });
  };

  render() {
    let now = new Date();
    let dname = now.getDay(),
      mo = now.getMonth(),
      dnum = now.getDate(),
      yr = now.getFullYear();

    let months = ["January", "February", "March", "April", "May", "June", "July", "Augest", "September", "October", "November", "December"];
    let week = ["Minggu", "Senin", "Selasa", "Rabu", "Kamis", "Jumat", "Sabtu"];
    let values = [week[dname], months[mo], dnum, yr];

    setInterval(() => {
      const timezone = this.state.jam; //needs to be converted in minutes
      const timezoneInMinutes = timezone / 60;
      const currTime = moment().utcOffset(timezoneInMinutes).format("h:mm:ss a");
      this.setState({ realjam: currTime });
    }, 1000);

    const cel = Math.ceil(this.state.temp - 273);
    const ico = `http://openweathermap.org/img/wn/${this.state.icon}.png`;

    return (
      <div className="container">
        <div className="row">
          <div className="card text-white mb-3 position-absolute top-50 start-50 translate-middle" style={{ maxWidth: "35rem" }}>
            <form onSubmit={this.onSubmitHandler} className="form d-flex" style={{ marginTop: "20px" }}>
              <input className="form-control" name="negara" value={this.state.negara} onChange={this.handlerinputOnChange} type="search" placeholder="Search City" aria-label="Search" />
              <button className="btn" type="submit">
                <i className="fa-solid fa-magnifying-glass"></i>
              </button>
            </form>
            <div className="d-flex" id="d-flex">
              <div className="card-body">
                <h1>{cel}&ordm;C</h1>
                <h5 className="card-title">Weather in {this.state.nama}</h5>
                <p className="card-text">
                  <img src={ico} alt="" />
                  {this.state.awan}
                </p>
                <article className="card-text">Humidity: {this.state.humidity}%</article>
                <article className="card-text">Wind: {this.state.wind} Km/h</article>
              </div>
              <div className="card-body nc">
                <article className="rl">Real Time Clock</article>
                <h3 className="rl">
                 {this.state.realjam}
                </h3>
                <p className="rl">
                  {values[0]}, {values[1]} {values[2]} {values[3]}
                </p>
              </div>
            </div>
            <hr />
            <p className="creator">Create By Ari Rachman With React Js </p>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
